import { Button, Drawer } from "@rp/react/web/mui/core"
import { useFeedbackContext } from "./FeedbackContext"
import { FeedbackBody, FeedbackContainer, FeedbackTitle } from "./FeedbackMode.styles"
import PersonaSubTypeForm from "./PersonaSubTypeForm"
import PersonaTypeForm from "./PersonaTypeForm"
import QueryDescriptionForm from "./QueryDescriptionForm"
import RankingModeForm from "./RankingModeForm"
import SearchIntentForm from "./SearchIntentForm"

const FeedbackDrawer = () => {
  const { open, setOpen, clear, rankingProfiles } = useFeedbackContext()

  const onDisable = () => {
    clear()
  }
  const handleDrawer = () => {
    setOpen(false)
  }

  return (
    <Drawer data-testid="feedback-drawer" anchor="right" open={open} onClose={handleDrawer}>
      <FeedbackContainer>
        <FeedbackBody>
          <FeedbackTitle variant="h6">
            Change the following parameters to send with the feedback
          </FeedbackTitle>
          <RankingModeForm rankingProfiles={rankingProfiles} />
          <PersonaTypeForm />
          <PersonaSubTypeForm />
          <SearchIntentForm />
          <QueryDescriptionForm />
        </FeedbackBody>

        <Button data-testid="close-drawer" onClick={handleDrawer}>
          Close
        </Button>
        <Button data-testid="disable-feedbackMode" onClick={onDisable}>
          Disable feedback mode
        </Button>
      </FeedbackContainer>
    </Drawer>
  )
}

export { FeedbackDrawer }
